export default {
	data() {
		return {
			date: new Date().toISOString().slice(0, 10),
			checked: true,
			shop: {},
			discount:'已优惠￥0.00'
		}
	},
	onLoad(option) {
		console.log(option)
		this.shop = option
	},
	methods: {
		clickNavi() {
			uni.navigateTo({
				url: '../index/time'
			})
		},
		onChange(e) {
			this.checked = e.detail
		},
		submit() {
			uni.showToast({
			  title: '提交成功',
			  icon: 'success',
			  duration: 2000
			})
		},
		getLocation() {
			let _this = this
			uni.openLocation({
				latitude: 35.321421,
				longitude: 113.909258,
				name: 'IFCha一夫茶（万达店）',
				address: '学院街30号L6商铺',
				success: function(res) {
					console.log(res);
				}
			});
		}
	}
}
